.MenuPage img {
  width: 4em;
  height: 4em;
}

#Courses {
  display: inline-flex;
}

#Course {
  display: inline-flex;
}

#Course div {
  width: 15em;
  text-align: left;
  border-bottom: solid 0.2em aqua;
  margin-left: 2em;
  margin-right: 0;
  margin-top: 2em;
  padding-right: 7em;
}

#Course div p {
  display: inline;
  font-size: 1em;
}

#Course div #CourseName {
  margin: 0;
  margin-right: 1rem;
  font-size: 3em;
}

#Course img {
  margin-top: auto;
}

.Image {
  display: inline-flex;
  margin-top: 2em;
  margin-left: 4em;
}

.Image img {
  width: 8em;
  height: 8em;
  margin-top: auto;
}

.MateCourses #Course p {
  color: blue;
}

.MateCourses #Course #CourseName {
  color: rgb(153, 188, 255);
}

.PhysicsCourses #Course p {
  color: green;
}

.PhysicsCourses #Course #CourseName {
  color: rgb(0, 255, 0);
}
